
import Button from "../Buttons/Button/Button";
import s from "./index.module.css";

export default function AllPrice(props) {

    const price = {
        price: 0,
        quality: 0,
        discount: 0,
        allPrice: 0
    };

    for (let i in props) {
        price.price += (props[i].price * props[i].quality);
        price.quality += props[i].quality;
        price.allPrice += Math.round(props[i].price - props[i].price * (props[i].discount / 100)) * props[i].quality;
        price.discount = Math.round(((price.price - price.allPrice) / price.price) * 100);
    }

    return (
        <div className={s.cart}>
            <h3 className={s.cart__title}> </h3>
            <div className={s.cart__price}>{` (${price.quality}): ${price.price}`}</div>
            <div className={s.cart__discount}>: {price.discount}</div>
            <h4 className={s.cart__allPrice}> : {price.allPrice}</h4>
            <Button> </Button>
        </div>
    )
}