import { useForm } from "react-hook-form";
import { useNavigate, useSearchParams } from "react-router-dom";
import Form from "../Form/Form";
import FormInput from "../FormInput/FormInput";
import Modal from "../Modal/Modal";
import { FORMOBJECT } from "../../Constant/Constant";
import s from "./index.module.css";
import ButtonForm from "../Buttons/Button/Button";
import ProtectedRouter from "../ProtectedRouter/ProtectedRouter";

export default function ResetPassword() {

    const { required: requiredForm, email: emailForm } = FORMOBJECT;
    const [url] = useSearchParams();
    const navigate = useNavigate();

    const { register, handleSubmit, reset, formState: { errors } } = useForm({ mode: "onBlur" });

    const email = register("email", {
        required: {
            value: true,
            message: requiredForm
        },
        pattern: {
            value: emailForm.pattern,
            message: emailForm.message
        }
    });

    //   
    function handleFormSubmit(e) {
        reset();
        navigate(-1);
    }

    return (
        url.get("reset_password") &&
        <ProtectedRouter>
            <Modal>
                <div className={s.modal__content}>
                    <Form title=" " handleSubmit={handleSubmit(handleFormSubmit)}>
                        <p className={s.infoText}>      email,   .</p>

                        <FormInput {...email} type="email" placeholder=" email" />
                        {errors?.email && <p className={s.error}>{errors.email.message}</p>}

                        <ButtonForm></ButtonForm>

                    </Form>
                </div>
            </Modal>
        </ProtectedRouter>
    )
}